<template>
   <div class="sale">
      <div class='mine_top'>
         <div class='return' @click='retreat()'></div>
         <span>创建卖单</span>
      </div>
      <div class='setmenu'>
         <div class='currency'>
            <p>卖出币种</p>
            <select v-model="takex">
               <option v-for='(item,index) in takes' :key='index'>{{item}}</option>
            </select>
         </div>
         <van-cell-group>
            <van-field
            label="卖出数量"
            v-model="sale_num"
            placeholder="请输入您要卖的token币数量" />
         </van-cell-group>
         <div class='currency'>
            <p>接受币种</p>
            <select v-model="take" >
               <option v-for='(item,index) in takes' :key='index'>{{item}}</option>
            </select>
         </div>
         <van-cell-group>
            <van-field
            label="单价"
            v-model="price"
            placeholder="请输入币的单价" />
         </van-cell-group>
         <van-cell-group>
            <van-field
            label="接受数量"
            disabled
            :placeholder=" (sale_num*price) == 0 ? '请输入接受数量' : (sale_num*price)" />
         </van-cell-group>
         <div class='currency'>
            <p>收款地址</p>
            <span v-if="num == ''">
               {{site}}
            </span>
            <span v-else-if="num !== ''" class='add'>
               <p v-if='num.ali_nickname != ""'>支付宝：{{num.ali_nickname}}-{{num.ali_account}}</p>
               <p v-if='num.we_nickname != ""'>微信：{{num.we_nickname}}-{{num.we_account}}</p>
               <p v-if='num.idcard != ""'>银联：{{num.idcard}}</p>
            </span>
         </div>
         <van-cell-group>
            <van-field
            label="手机号"
            disabled
            :placeholder="phone" />
         </van-cell-group>
            <div class='site'></div>
            <!-- <div class='getsite getsite1'>
               <p>
                  <van-icon @click='err_btn(1)' class='err' name="cross" />
                  添加收款地址
               </p>
               <div class='import'>
                  <div>
                     <p>接受币种</p>
                     <select v-model="currency">
                        <option>支付宝</option>
                        <option>微信</option>
                     </select>
                  </div>
                  <div>
                     <p>收款地址</p>
                     <input v-model="site" type="text" placeholder='请输入地址'>
                  </div>
                  <p>请务必确保地址正确</p>
               </div>
               <van-button @click='site_btn()' class='site_btn' size="large">确认</van-button>   
            </div>    -->
            <div class='getsite getsite2'>
               <p  style='margin-bottom:50px;'>
                  <van-icon @click='err_btn(2)' class='err' name="cross" />
                  请输入支付密码
               </p>
               <!-- 密码输入框 -->
               <van-cell-group>
                  <van-field v-model="value" type='password' placeholder="请输入您的交易密码" />
               </van-cell-group>
               <!-- 确认键 -->
               <van-button class='btn' @click='Submit()' size="large">确认密码</van-button>
            </div>       
      </div>
      <van-button class='btn' @click='sale(2)' size="large">确认创建</van-button>
   </div>
</template>

<script>

import axios from 'axios'

export default {
   name: '',
   data() {
       return {
          phone:localStorage.phone,
          currency:'支付宝',   // 接受方式
          site:'请输入您要收款的地址',            // 接受地址
          price:'',           // 单价
          takex:'',           // 要卖的币种
          take:'',       //  支付的币种
          takes:[],            // 币种类型
          sale_num:'',        // 卖出数量
          showKeyboard: true,
          value:'',  // 支付密码
          num:''   // 
       }
   },
   watch:{
      take(New,Old){
         // console.log(New)
         let url =  window.globalData.config.domain + '/user/findWithdrawAddress';
         axios.post(url,{
            cmd:'123',
            body:{
               appid:localStorage.appid,
               uid:localStorage.uid,
               token:New
            }
         }).then( (res) => {
            // console.log(res.data);
            let data = res.data;
            if( data === null ){
               this.$dialog.alert({
                  message: '您未绑定该地址,无法选择'
               })
               this.take = '';
               this.num = '';
               this.site = '请输入您要收款的地址';
            }else if( New === 'CNY' ){
               this.site = '';
               this.num = data;
            }else if( data !== null ){
               this.num = ''
               this.site = data;
            }
         })
      },
      price(New,Old){
         if( !/^[0-9]*$/.test(New) ){
            this.$dialog.alert({
               message: '请输入正确价格'
            })
            this.price = '';
         }
      },
      sale_num(New,Old){
         if( !/^[0-9]*$/.test(New) ){
            this.$dialog.alert({
               message: '请输入正确价格'
            })
            this.sale_num = '';
         }
      }
   },
   created(){
      this.Currency();
   },
   methods:{
      retreat(){
         this.$router.go(-1);
      },
      // 支付vant事件
      Submit(){
            let url = window.globalData.config.domain + '/otc/sellPendOrder';
            axios.post(url,{
               cmd:'201',
               body:{
                  accessToken:localStorage.token,     // token
                  from_token:this.takex,    // 要卖的币种
                  token_amount:this.sale_num,                                  // 要卖的数量
                  dst_token:this.take,                         // 要换的币种
                  dst_token_amount:this.sale_num*this.price,                             // 要换的数量
                  price:this.price,                                // 单价
                  phone:this.phone,                            // 手机号码                       
                  paypasswd:this.value                             // 支付密码
               }
            }).then( (res) => {
               // console.log(res);
               if( res.data.code === 217 ){
                  this.$dialog.alert({
                     message: '密码输入错误'
                  })
                  this.value = '';
               }else if( res.data.code === 116 ){
                  this.$dialog.alert({
                     message: '您的余额不足'
                  })
                  this.value = '';
               }else if( res.data.code === 104 ){
                  this.$dialog.alert({
                     message: '您填写的信息有误'
                  })
                  this.value = '';
               }else if( res.data.code === 132 ){
                  this.$dialog.alert({
                     message: '请绑定支付宝、微信或者银行卡'
                  })
                  this.$router.go(-1);
               }else if( res.data.code === 133 ){
                  this.$dialog.alert({
                     message: '请绑定'+this.take+'地址'
                  })
                  this.$router.go(-1);
               }else if( res.data.code === 0 ){
                  this.$router.go(-1);
               }else{
                  this.$dialog.alert({
                     message: '订单创建失败'
                  })
                  this.$router.go(-1);
               }
            })
      },
      setsite(index){
         let site = document.getElementsByClassName('site')[0];
         let getsite = document.getElementsByClassName('getsite'+index)[0];
         site.style.display = 'block';
         getsite.style.display = 'block';
      },
      err_btn(index){
         let site = document.getElementsByClassName('site')[0];
         let getsite = document.getElementsByClassName('getsite'+index)[0];
         site.style.display = 'none';
         getsite.style.display = 'none';
      },
      sale(index){
         if( this.takex === '' ){
            this.$dialog.alert({
               message: '请选择您要卖出的货币种类'
            })
         }else if( this.sale_num === '' ){
            this.$dialog.alert({
               message: '请输入您要交易的货币数量'
            })
         }else if( this.price === '' ){
            this.$dialog.alert({
               message: '请输入您出售的货币单价'
            })
         }else if( this.take === '' ){
            this.$dialog.alert({
               message: '请选择您要接受的货币种类'
            })            
         }else if( this.site == '请输入您要收款的地址' ){
            this.$dialog.alert({
               message: '请填写您的收款地址'
            })
         }else if( this.takex === this.take ){
            this.$dialog.alert({
               message: '不能购买、售卖相同的货币'
            })
         }else{
            this.setsite(index);
         }
      },
      Currency(){
         let url = '/blockchain/getSupportCoins';
         this.$http.post(url,{
            cmd:'',
            body:{}
         }).then( (res) => {
            this.takes = res;
            // console.log(this.take)
         })
      },
   },
}
</script>

